<template>
    <div id="app">
        <div class="container">
            <h3 class="text-uppercase text-center text-danger">
                Category:
                <select v-model="category" v-on:change="changeCat">
                    <option v-for="option in options" :value="option.id">
                        {{ option.name }}
                    </option>
                </select>
            </h3>

            <div class="jumbotron">
                <div class="num"
                     v-if="data.length > 0"
                     v-bind:class="{ red: isEven, blue: !isEven }">
                    {{ wordCnt + 1 }}
                </div>
                <h2 v-if="lang.ru">{{ current.ru }}</h2>
                <h2 v-if="lang.en">{{ current.en }}</h2>
            </div><!-- .jumbotron -->

            <p>
                <button @click="prevWord" type="button" class="btn btn-lg btn-default pull-left">
                    <i aria-hidden="true" class="fa fa-backward fa-2x green"></i>
                </button>
                <button @click="startWord" type="button" class="btn btn-lg btn-default">
                    <i aria-hidden="true" class="fa fa-stop-circle-o fa-2x red"></i>
                </button>
                <button @click="nextWord" type="button" class="btn btn-lg btn-default pull-right">
                    <i aria-hidden="true" class="fa fa-forward fa-2x green"></i>
                </button>
            </p>
            <p v-if="data.length > 0" id="change_lang">
                <button @click="switchLang" type="button" class="btn btn-lg btn-success btn-block">
                    {{ nextLang }}
                </button>
            </p>


        </div><!-- .container -->

    </div><!-- #app -->
</template>

<script>
    import _ from 'underscore';

    export default {
        name: 'app',
        data() {
            return {
                lang: {
                    ru: true,
                    en: false
                },
                wordCnt: 0,
                options: Category,
                category: 0,
                data: [],
                current: {
                    ru: "",
                    en: ""
                },
            }
        },
        computed: {
            nextLang() {
                return this.lang.ru ? 'En' : 'Ru';
            },
            WordsLength() {
                return this.data.length;
            },
            isEven() {
                return !(this.wordCnt % 2);
            }
        },
        methods: {
            switchLang: function () {
                this.lang.en = !this.lang.en;
                this.lang.ru = !this.lang.ru;
            },
            prevWord: function () {
                this.wordCnt--;
                if (this.wordCnt < 0) {
                    this.wordCnt = this.WordsLength - 1;
                }
                this.changeWord();
            },
            nextWord: function () {
                this.wordCnt++;
                if (this.wordCnt >= this.WordsLength) {
                    this.wordCnt = 0;
                }
                this.changeWord();
            },
            startWord: function () {
                this.wordCnt = 0;
                this.changeWord();
            },
            changeWord: function () {
                this.current = this.data[this.wordCnt];
            },
            changeCat: function (event) {
                this.data = _.where(Data, {category: this.category});
                this.wordCnt = 0;
                this.changeWord();
                this.lang = {
                    ru: true,
                    en: false
                };
            }
        }

    }
</script>

<style lang="scss">
    .jumbotron {
        position: relative;

        .num {
            position: absolute;
            left: 10px;
            top: 10px;
            font-size: 20px;
        }
    }//.jumbotron

    #change_lang{
        margin-top: 40px;
    }

    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }

    h1, h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }

    .red {
        color: red;
    }

    .green {
        color: green;
    }

    .blue {
        color: blue;
    }
</style>



